import React from 'react';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Slider from "@material-ui/core/Slider";
import {IconPack} from '../../Images/IconPack';
import SignIn from './SignIn';
import SignUp from './SignUp';
import SetNewPassword from './SetNewPassword';
import SetNewEmail from './SetNewPassword';
import ResetPass from './ResetPass';
import {connect} from "react-redux";


const AuthWindow = (props) => {

    let modalWindow = null;

    if(props.appState.authModals.resetPass) {
        modalWindow = (<ResetPass />);
    }else if(props.appState.authModals.setNewEmail) {
        modalWindow = (<SetNewEmail />);
    }else if(props.appState.authModals.setNewPassword) {
        modalWindow = (<SetNewPassword />);
    }else if(props.appState.authModals.signIn) {
        modalWindow = (<SignIn />);
    }else if(props.appState.authModals.signUp) {
        modalWindow = (<SignUp />);
    }


    if(modalWindow) {
       return (<div className="AuthWindow">
                <div className="logo">
                   {IconPack.menuIcons.logo}
                </div>
                {modalWindow}
           </div>);
    }else {
        return null;
    }
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {

    }
}

export default connect(mapStateToProps, mapDispatchToProps)(AuthWindow);