import React from 'react';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import ReCAPTCHA from "react-google-recaptcha";
import Switch from "@material-ui/core/Switch/Switch";
import {setDashboard, setResetPassAuthModal, setSignInAuthModal, setSignUpAuthModal} from "../../redux/app/app.actions";
import {connect} from "react-redux";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';



const SignUp = (props) => {

    const [value1, setValue1] = React.useState();
    const [value2, setValue2] = React.useState();
    const [value3, setValue3] = React.useState();
    const [value4, setValue4] = React.useState();
    const [value5, setValue5] = React.useState();
    const [value6, setValue6] = React.useState(true);

    const toRegisterHandler = () => {
        props.setSignUpAuthModal(false);
        props.setDashboard(true);
    };

    return (
        <div className="authModal w400">
            <header>
                <div className="active"><FormattedMessage id='button.signUp' /></div>
                <div onClick={()=>{
                    props.setSignUpAuthModal(false);
                    props.setSignInAuthModal(true);
                }}><FormattedMessage id='button.signIn' /></div>
            </header>
            <main>
                <FormattedMessage  id='auth.login'>
                    {(msg) => (   <TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value1}
                        onChange={(e)=>{ setValue1(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>
                <div className="doubled">

                    <FormattedMessage  id='auth.password'>
                        {(msg) => (   <TextField
                            variant="filled"
                            fullWidth
                            label={msg}
                            value={value2}
                            type="password"
                            onChange={(e)=>{ setValue2(e.target.value)}}>
                        </TextField>)}
                    </FormattedMessage>



                    <FormattedMessage  id='auth.passwordAgain'>
                        {(msg) => (   <TextField
                            variant="filled"
                            fullWidth
                            label={msg}
                            value={value3}
                            type="password"
                            onChange={(e)=>{ setValue3(e.target.value)}}>
                        </TextField>)}
                    </FormattedMessage>

                </div>
                <FormattedMessage  id='auth.email'>
                    {(msg) => (   <TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value4}
                        type="password"
                        onChange={(e)=>{ setValue4(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>

                <FormattedMessage  id='auth.promocode'>
                    {(msg) => (<TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value5}
                        type="password"
                        onChange={(e)=>{ setValue5(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>

            </main>
            <footer>
                <div className="switch-wrapper">
                <Switch checked={ value6 }
                        name="autoRenew"
                        onChange={(e)=>{ setValue6(!value6)}} />
                <p><span className="color-white">I accept</span> terms of service</p>
                </div>

                <Button variant="contained" className="btn btn--blue" onClick={()=>{toRegisterHandler()}}>
                    <FormattedMessage id='button.toRegister' />
                </Button>
            </footer>
        </div>
    );
};


const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setSignInAuthModal: (bool) => dispatch(setSignInAuthModal(bool)),
        setSignUpAuthModal: (bool) => dispatch(setSignUpAuthModal(bool)),
        setResetPassAuthModal: (bool) => dispatch(setResetPassAuthModal(bool)),
        setDashboard: (bool) => dispatch(setDashboard(bool)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(SignUp);