import React from 'react';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import ReCAPTCHA from "react-google-recaptcha";
import {setSignInAuthModal, setChangePasswordAuthModal, setDashboard} from "../../redux/app/app.actions";
import {connect} from "react-redux";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';


const SetNewPassword = (props) => {

    const [value1, setValue1] = React.useState();
    const [value2, setValue2] = React.useState();

    const handleChange1 = (event, newValue) => {
        setValue1(newValue);
    };

    const handleChange2 = (event) => {
        setValue2(event.value);
    };

    const cancelHandler = () => {

        props.setChangePasswordAuthModal(false);
       if(!props.appState.dashboard){
           props.setSignInAuthModal(true);
       }

    }

    const changePassHandler = () => {
        props.setChangePasswordAuthModal(false);
        if(!props.appState.dashboard){
            props.setSignInAuthModal(true);
        }
    }

    return (
        <div className="authModal w400">
            <header>
                <p><FormattedMessage  id='auth.setANewPassword' /></p>
            </header>
            <main>
                <FormattedMessage  id='auth.newPassword'>
                    {(msg) => (<TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value1}
                        type="password"
                        onChange={(e)=>{handleChange1(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>
                <FormattedMessage  id='auth.newPasswordAgain'>
                    {(msg) => (<TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value2}
                        type="password"
                        onChange={(e)=>{handleChange2(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>

            </main>
            <footer>
                <Button variant="contained" className="btn btn--blue" onClick={()=>{changePassHandler()}}>
                    <FormattedMessage id='button.changePassword' />
                </Button>
                <div className="btn btn--text" onClick={()=>{cancelHandler()}}><FormattedMessage id='button.cancelChangePassword' /></div>
            </footer>
        </div>
    );
};


const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setSignInAuthModal: (bool) => dispatch(setSignInAuthModal(bool)),
        setChangePasswordAuthModal: (bool) => dispatch(setChangePasswordAuthModal(bool)),
        setDashboard: (bool) => dispatch(setDashboard(bool)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(SetNewPassword);