import React from 'react';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import ReCAPTCHA from "react-google-recaptcha";
import Button from '@material-ui/core/Button';
import {setSignInAuthModal, setSignUpAuthModal, setResetPassAuthModal, setDashboard} from "../../redux/app/app.actions";
import {connect} from "react-redux";
import { FormattedMessage } from 'react-intl';



const SignIn = (props) => {

    const [value1, setValue1] = React.useState('');
    const [value2, setValue2] = React.useState('');


    return (
        <div className="authModal w400">
            <header>
                <div onClick={()=>{
                    props.setSignInAuthModal(false);
                    props.setSignUpAuthModal(true);
                }}><FormattedMessage id='button.signUp' /></div>
                <div className="active"><FormattedMessage id='button.signIn' /></div>
            </header>
            <main>
                <FormattedMessage  id='auth.loginOrEmail'>
                    {(msg) => (   <TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value2}
                        onChange={(e)=>{setValue2(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>
                <FormattedMessage  id='auth.password'>
                    {(msg) => (   <TextField
                        variant="filled"
                        fullWidth
                        label={msg}
                        value={value1}
                        type="password"
                        onChange={(e)=>{setValue1(e.target.value)}}>
                    </TextField>)}
                </FormattedMessage>


            </main>
            <footer>
                <Button variant="contained" className="btn btn--blue" onClick={()=>{
                    props.setSignInAuthModal(false);
                    props.setDashboard(true);
                }}>
                    <FormattedMessage id='button.signIn' />
                </Button>

                <div className="btn btn--text" onClick={()=>{
                    props.setSignInAuthModal(false);
                    props.setResetPassAuthModal(true);
                }}><FormattedMessage id='button.forgotPassword' /></div>
            </footer>
        </div>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setSignInAuthModal: (bool) => dispatch(setSignInAuthModal(bool)),
        setSignUpAuthModal: (bool) => dispatch(setSignUpAuthModal(bool)),
        setResetPassAuthModal: (bool) => dispatch(setResetPassAuthModal(bool)),
        setDashboard: (bool) => dispatch(setDashboard(bool)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(SignIn);